import * as Yup from 'yup';
import { useState } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack, Tab, Tabs, Button, Typography, IconButton } from '@mui/material';
import { styled } from '@mui/material/styles';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import CloseIcon from '@mui/icons-material/Close';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';
import UserSearch from './UserSearch';
import Scrollbar from '../../components/Scrollbar';
import HabukhanUser from './HabukhanUser';
// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';

// ----------------------------------------------------------------------

const VisuallyHiddenInput = styled('input')({
  clip: 'rect(0 0 0 0)',
  clipPath: 'inset(50%)',
  height: 1,
  overflow: 'hidden',
  position: 'absolute',
  bottom: 0,
  left: 0,
  whiteSpace: 'nowrap',
  width: 1,
});

export default function System() {
  const { enqueueSnackbar } = useSnackbar();
  const isMountedRef = useIsMountedRef();
  const [status, setStatus] = useState('ALL');
  const [selectedImage, setSelectedImage] = useState(null);
  const [imagePreview, setImagePreview] = useState(null);

  const NewUserSchema = Yup.object().shape({
    message: Yup.string().required(),
  });


  const AccessToken = window.localStorage.getItem('accessToken');
  const [userUsername, SetUserUsername] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [isSearchFocused, setSearchFocused] = useState(false);
  const displayResults = userUsername && isSearchFocused;
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  const ACCOUNT_TABS = [
    {
      value: 'ALL',
      label: 'ALL USERS'
    },
    {
      value: 'SMART',
      label: 'SMART USER'
    },
    {
      value: 'AWUF',
      label: 'AWUF USERS'
    },
    {
      value: 'API',
      label: 'API USERS'
    },
    {
      value: 'SPECIAL',
      label: 'SPECIAL USERS'
    },
    {
      value: 'CUSTOM',
      label: 'CUSTOM'
    }
  ];
  const {
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const handleImageChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      setSelectedImage(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleRemoveImage = () => {
    setSelectedImage(null);
    setImagePreview(null);
  };

  const onSubmit = async (data) => {
    try {
      const formData = new FormData();
      formData.append('status', status);
      formData.append('message', data.message);
      formData.append('user_username', userUsername);

      if (selectedImage) {
        formData.append('image', selectedImage);
      }

      await axios.post(`/api/system/sendmessage/${AccessToken}/habukhan/secure`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });

      enqueueSnackbar('Notification sent successfully');
      setSelectedImage(null);
      setImagePreview(null);
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };

  const handleSearchFocus = () => {
    setSearchFocused(true);
  };

  const handleChangeSearch = async (event) => {
    const { value } = event.target;
    SetUserUsername(value);
    if (value) {
      try {
        const response = await axios.post(`/api/filter/user/details/admin/by/habukhan/${AccessToken}/secure/react`, {
          username: value
        });
        setSearchResults(response.data.user);
      } catch (error) {
        console.log(error)
      }
    } else {
      setSearchResults([]);
    }

  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item >
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            > <Tabs
              value={status}
              scrollButtons="auto"
              variant="scrollable"
              allowScrollButtonsMobile
              onChange={(e, value) => setStatus(value)}
            >
                {ACCOUNT_TABS.map((tab) => (
                  <Tab disableRipple key={tab.value} label={tab.label} value={tab.value} />
                ))}
              </Tabs>
              {status === 'CUSTOM' && <UserSearch
                query={userUsername}
                onChange={handleChangeSearch}
                onFocus={handleSearchFocus}
              />}
              {displayResults &&
                <Scrollbar>
                  <HabukhanUser query={userUsername} results={searchResults} onSelectContact={(e) => {
                    SetUserUsername(e.username); setSearchFocused(false)
                  }} />
                </Scrollbar>}
              <RHFTextField name="message" label="Message" rows={4} multiline />

              {/* Image Upload Section */}
              <Box sx={{ mt: 2 }}>
                <Typography variant="subtitle2" sx={{ mb: 1 }}>
                  Notification Image (Optional)
                </Typography>
                <Button
                  component="label"
                  variant="outlined"
                  startIcon={<CloudUploadIcon />}
                  fullWidth
                  sx={{ mb: 2 }}
                >
                  Upload Image
                  <VisuallyHiddenInput type="file" accept="image/*" onChange={handleImageChange} />
                </Button>

                {imagePreview && (
                  <Box sx={{ position: 'relative', mt: 2 }}>
                    <IconButton
                      onClick={handleRemoveImage}
                      sx={{
                        position: 'absolute',
                        top: 8,
                        right: 8,
                        bgcolor: 'background.paper',
                        '&:hover': { bgcolor: 'error.main', color: 'white' },
                      }}
                    >
                      <CloseIcon />
                    </IconButton>
                    <img
                      src={imagePreview}
                      alt="Preview"
                      style={{
                        width: '100%',
                        maxHeight: 300,
                        objectFit: 'contain',
                        borderRadius: 8,
                        border: '1px solid #e0e0e0',
                      }}
                    />
                  </Box>
                )}
              </Box>
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Send Notification
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
